.button {
  outline: unset;
  border: none;
  background-color: rgba(77, 57, 255, 0.63);
  width: 140px;
  height: 60px;
  border-radius: 15px;
  font-size: 18px;
  transition: all .3s;

  &:hover {
    box-shadow: 0px 0px 10px 4px white;
    cursor: pointer;
    transition: all .3s;
  }
}