@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans.ttf");
}

@mixin rusfont($size, $weight:false) {
  font-size  : $size;
  font-family: OpenSans;

  @if $weight {
    font-weight: $weight;
  }
}

@mixin default-box-shadow($color: white) {
  box-shadow: 0px 0px 10px 4px $color;
}